#wrapper {
   width: 100%;
   height: 100%;
   display: flex;
   flex-direction: column;
    color: white;
 }
#header {
  padding: 12px 12px;
    background-color: #000422;
  border-bottom: 1px solid white;
  box-sizing: border-box;
}
#field {
  flex-grow: 1;
}
#field svg {
  height: 100%;
  width: 100%;
  display: block;
}
#field polygon {
  cursor: pointer;
}