html {
  width: 100%;
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
#wrapper {
   width: 100%;
   height: 100%;
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
    color: white;
 }
#header {
  padding: 12px 12px;
    background-color: #000422;
  border-bottom: 1px solid white;
  box-sizing: border-box;
}
#field {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}
#field svg {
  height: 100%;
  width: 100%;
  display: block;
}
#field polygon {
  cursor: pointer;
}
#header-wrapper {
    display: -webkit-flex;
    display: flex;
}
span {
}
.requirement-wrapper {
    margin: 0 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    font-weight: bold;
}
.requirement-status {
    padding: 0 8px;
}
.requirement-status-overdone {
    color: #ff3c3c;
}
.requirement-status-completed {
    color: #3c3c3c;
    text-decoration: line-through;
}
svg {
    width: 16px;
    height: 16px;
}
#field {
    background-color: #000422;
}
.vertex-circle {
    cursor: pointer;
}
line {
    -webkit-animation: dash 5s linear forwards;
            animation: dash 5s linear forwards;
}
